.privacy-policy-main {
  padding: 2rem;
  max-width: 720px;
  margin: auto;
  color: #222;
  background: #f9fafb;
}
.privacy-policy-main h1,
.privacy-policy-main h2 {
  color: #1d4ed8;
}
.privacy-policy-main h1 {
  font-size: 2.5rem;
  margin-bottom: 1rem;
  font-weight: 800;
}
.privacy-policy-main h2 {
  font-size: 1.4rem;
  margin-top: 2rem;
  margin-bottom: 0.8rem;
  font-weight: 700;
}
.privacy-policy-main ul {
  margin-left: 1.25rem;
}
.privacy-policy-main a {
  color: #1d4ed8;
}